import Vue from 'vue'
import Router from 'vue-router'
import SignUp from "../components/SignUp";
import Login from "../components/Login";
import UserList from "../components/UserList";

Vue.use(Router);

export const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'Login',
            component: Login
        },
        {
            path: '/signUp',
            name: 'SignUp',
            component: SignUp
        },
        {
            path: '/userList',
            name: 'UserList',
            component: UserList
        }
    ]
})